import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import { ProgressBar } from 'react-bootstrap';
import db from 'libs/db';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import cssVar from 'views/mobileView/styles';
import AdminServices from 'services/api/AdminServices';
import DOBInputMask from 'views/mobileView/components/atoms/DOBInputMask';
import { Chip, Stack } from '@mui/material';
import cloudPng from 'assets/img/cloud.png';


const DocumentUpload = (props) => {
    const {taxStatus} = props.location.state
    const [panpic, setpanpic] = React.useState('');
    const [panID, setpanID] = React.useState('');

    const [chequepic, setchequepic] = React.useState('');
    const [chequeID, setchequeID] = React.useState('');
    var inputFilePan = React.useRef(null)
    var inputFileChe = React.useRef(null)
    const [user, setUser] = React.useState({});

    const [nextDisabled,setNextDisabled]=React.useState(false)
    const [loading,setloading] = React.useState();
    const [modalType, setmodalType] = React.useState('lump');

    const [ShowComment, setModelComment] = React.useState(false);
    React.useEffect(() => {
		db.get(db.model.userData)
        .then((u) => {
			if (u) {
				if(u.doc){
                    setpanpic(u.doc.panpic || '');
                    setchequepic(u.doc.chequepic || '');
                    setpanID(u.doc.panID || '');
                    setchequeID(u.doc.chequeID || '');
                }
			};
		})
        .then(() => {
            db.get(db.model.user, true).then((p) => {
                setUser(p.userData);
                setloading(false);
            });
        });
	}, []);

    React.useEffect(() => {
		checkValidation();
	}, [panID,chequeID]);

    const checkValidation = () => {
        if (
            panID !== ''
            && panID !== null
            && chequeID !== ''
            && chequeID !== null
        ) {
            setNextDisabled(false);
        } else {
            setNextDisabled(true);
        }
    };
    const uploadImage = async (singleFile, modalType) => {
        // Check if any file is selected or not
        if (singleFile != null) {
            // If file selected then create FormData
            const fileToUpload = singleFile;
            const data = new FormData();
            data.append('file', fileToUpload);

            //   var res = await AdminServices.uploadDoc(user.id,data).then((res) => {return res})
            //  console.log(modalType,res.data)

            try {
                const res = await AdminServices.uploadDoc(user.id, data).then((res) => res);
                console.log(res.data);
                if (res.type) {
                    if (res.data.DocumentId) {
                        if (modalType === 'pan') {
                            setpanID(res.data.DocumentId);
                        } else {
                            setchequeID(res.data.DocumentId);
                        }
                    } else {
                        console.log('ERR : 2', res.msg);
                        toast.error("try again...")
                    }
                } else {
                    console.log('ERR : 1', res.msg);
                    toast.error("try again...")

                }
            } catch (error) {
                console.log('ERR : c', error);
                toast.error("try again...")

            }
        } else {
            // if no file selected the show alert
            alert('Please Select File first');
        }
    };

    const imagePick = (event) => {
        // ImagePicker.openPicker({
        //     width: s.wp('100%'),
        //     height: s.hp('30%'),
        //     cropping: true
        // }).then((image) => {
        //     const file = {
        //         uri: image.path,
        //         name: 'pan.jpg',
        //         type: image.mime
        //     };

        if (modalType === 'pan') {
            setpanpic(URL.createObjectURL(event.target.files[0]));
            var file = event.target.files[0];

            uploadImage(file, modalType);
            setModelComment(false);
        } else {
            setchequepic(URL.createObjectURL(event.target.files[0]));

            var file = event.target.files[0];

            uploadImage(file, modalType);
            setModelComment(false);
        }
    };
    return (
        <div>
            <MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
            <ProgressBar now={90} className="progress-bar-custom" />

            <div className="containerData mt-4" style={{padding:"4%",marginBottom:"72px"}}>
                <div className="textCardHeader" style={{marginBottom:"4%"}}>{taxStatus==='1'?"Upload legal guardian PAN":"Upload PAN"}</div>
                {panpic === '' || panpic === null ? (
                    <div
                        style={{
                            borderWidth: 1,
                            borderStyle: 'dashed',
                            borderColor: cssVar.getPropertyValue("--primary"),
                            borderRadius: 10,
                            backgroundColor: cssVar.getPropertyValue("--cardColor"),
                            alignItems: 'center',
                            padding: "30px",
                            display: "flex",
                            justifyContent: "space-around"
                        }}
                        onClick={() => {
                            inputFilePan.current.click();
                            setModelComment(true);
                            setmodalType('pan');
                        }}>
                        <>
                            <input type='file' accept="image/*" id='panfile' ref={inputFilePan} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

                            <span
                                style={{

                                    color: cssVar.getPropertyValue("--primary")
                                }}>
                                Take a photo or upload an image
                            </span>
                            <img

                                resizeMode="contain"
                                src={cloudPng} />
                        </>
                    </div>
                ) : (
                    <div
                        style={{
                            borderWidth: 1,
                            borderStyle: 'dashed',
                            borderColor: cssVar.getPropertyValue("--primary"),
                            borderRadius: 10,
                            backgroundColor: cssVar.getPropertyValue("--cardColor"),
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "space-around",
                            height: "10px !important",


                        }}>
                        <img
                            width="100%" height="160"

                            style={{
                                borderRadius: 10
                            }}
                            src={panpic}

                        />
                        <div
                            style={{
                                alignItems: 'center',
                                padding: "10px",
                                display: "flex",
                                justifyContent: "space-around"
                            }}
                            onClick={() => {
                                inputFilePan.current.click();
                                setModelComment(true);
                                setmodalType('pan');
                            }}>
                            <>
                                <input type='file' accept="image/*" id='panfile' ref={inputFilePan} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

                                <span
                                    style={{
                                        color: cssVar.getPropertyValue("--primary")
                                    }}>
                                    Upload a different image
                                </span>
                                <img

                                    resizeMode="contain"
                                    src={cloudPng} />
                            </>
                        </div>
                    </div>
                )}
                    <div className="textCardHeader" style={{marginTop:"10%",marginBottom:"4%"}}>Upload cancelled Cheque</div>

                    {/* <Icon
                        style={{ ...s.mt('2.5%'), ...s.mh('1%') }}
                        name="info"
                        color={cssVar.getPropertyValue("--primary")}
                        size={15}
                        onClick={() => {
                            setdtitle(
                                'Mutual fund companies need to keep a record of your bank accounts. They require you to submit any one of the following'
                            );
                            setddata([
                                'A cancelled cheque with your name printed on the cheque',
                                'A clear copy of the first page of bank account passbook (with your name printed and account number visible)',
                                'A clear copy of the bank account statement (with your name printed and account number visible)'
                            ]);
                            setdshow(true);
                        }} /> */}

                {chequepic === '' || chequepic === null ? (
                    <div
                        style={{
                            borderWidth: 1,
                            borderStyle: 'dashed',
                            borderColor: cssVar.getPropertyValue("--primary"),
                            borderRadius: 10,
                            backgroundColor: cssVar.getPropertyValue("--cardColor"),
                            alignItems: 'center',
                            padding: "30px",
                            display: "flex",
                            justifyContent: "space-around"
                        }}
                        onClick={() => {
                            inputFileChe.current.click();
                            setModelComment(true);
                            setmodalType('cheque');
                        }}>
                        <>
                            <input type='file' accept="image/*" id='chequefile' ref={inputFileChe} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

                            <span
                                style={{

                                    color: cssVar.getPropertyValue("--primary")
                                }}>
                                Take a photo or upload an image
                            </span>
                            <img

                                resizeMode="contain"
                                src={cloudPng} />
                        </>
                    </div>
                ) : (
                    <div
                        style={{
                            borderWidth: 1,
                            borderStyle: 'dashed',
                            borderColor: cssVar.getPropertyValue("--primary"),
                            borderRadius: 10,
                            backgroundColor: cssVar.getPropertyValue("--cardColor"),
                            display: "flex",
                            flexDirection: "column",
                            justifyContent: "space-around",
                            height: "10px !important",


                        }}>
                        <img
                            width="100%" height="160"
                            style={{
                                borderRadius: 10
                            }}
                            src={chequepic}

                        />
                        <div
                            style={{
                                alignItems: 'center',
                                padding: "10px",
                                display: "flex",
                                justifyContent: "space-around"
                            }}
                            onClick={() => {
                                inputFileChe.current.click();
                                setModelComment(true);
                                setmodalType('cheque');
                            }}>
                            <>
                                <input type='file' accept="image/*" id='chequefile' ref={inputFileChe} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

                                <span
                                    style={{
                                        color: cssVar.getPropertyValue("--primary")
                                    }}>
                                    Upload a different image
                                </span>
                                <img

                                    resizeMode="contain"
                                    src={cloudPng} />
                            </>
                        </div>
                    </div>
                )}
            
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next="/activate-direct-mf/sign-upload"
                    params={{taxStatus:taxStatus}}
                    saveData={{
                        doc:{
                            panpic,
                            panID,
                            chequepic,
                            chequeID
                        }
                    }}/>
            </div>
        </div>
    )
}

export default DocumentUpload
