import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';


export default function CapabilityPage3(props) {

	const [income, setIncome] = React.useState("");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "0", "name":"No Income"},
		{ "key": "1200000", "name": "₹ 12 Lakh" },
		{ "key": "1800000", "name": "₹ 18 Lakh" },
		{ "key": "2400000", "name": "₹ 24 Lakh" }
	]

	const checkValidation = () => {
		if (income) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [income])

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setIncome(u.wealthCreator.income || '')
				}
			}
			setLoading(false);
		});
	}, []);

	return (
		<div>
			<MainHeader
				header="Risk taking capability"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={27} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">Your annual income</div>
							<input
								type="text"
								className='normalInput'
								style={{color:"#294C88"}}
								placeholder='12,00,000'
								id="income"
								value={income}
								onChange={(num) => {
									const a = num.target.value.replace(/\D/g, '')
									setIncome(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row' , overflowX:"auto"}}>
								{chipset.map((item) => {
									return (
										<Chip
											className="chip-b"
											label={item.name}
											onClick={() => {
												setIncome(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-capability-4"
				params={props.location.state}
				buttonText="Next"
				saveData={{
					wealthCreator: {
						income
					}
				}}
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
