import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import { ProgressBar } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
const TaxStatus = (props) => {
    const [taxStatus,setTaxStatus]=React.useState('0');
    const data = [
		{
			key: '0',
			data: 'Indian Resident - adult'
		},
		{
			key: '1',
			data: 'Indian Resident - minor'
		},
		{
			key: '2',
			data: 'NRI - adult'
		}
	];

    React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
			if (u) {
                    setTaxStatus(u.taxStatus || '0')
			};
		})
	}, []);

    return (
        <div>
            <MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
			<ProgressBar now={10} className="progress-bar-custom" />
            <div className="containerData my-4" style={{padding:"4%"}}>
            <div className="textCardHeader">Select investor tax status</div>
				
				{data.map((obj) => (
					<div
						key={obj.key}
						style={{
							display: 'flex',
							flexDirection: 'row',
							marginTop:"6%"
						}}>
						<div className="radio-item">
							<input
								type="radio"
								id={obj.key}
								name="ritem"
								value={obj.key}
								onChange={(val) => {
									setTaxStatus(val.target.value)
								}}
								checked={taxStatus === obj.key}
								className="textNormal"
							/>
							<label className="textNormal radio-label" htmlFor={obj.key}>{obj.data}</label>
						</div>
					</div>
				))}
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    navigation={props.history}
                    buttonText="Confirm"
                    next={taxStatus==='1'?"/activate-direct-mf/name":"/activate-direct-mf/pan"}
                    params={{taxStatus:taxStatus}}
                    saveData={{
						taxStatus : taxStatus
                    }}/>
            </div>
        </div>
    )
}

export default TaxStatus
