import React from 'react'
import {SmallBlueCube} from 'assets/SVG/index'
const HideShowBox = ({
    headerText,
    subHeaderArray
}) => {

    const [showContent,setShowContent]=React.useState(true);

    return (
        <div style={{background:"#F4F8FF",border:"1px solid #e5e5e5", borderRadius:"6px",padding:"4%"}}>
            <div style={{display:"flex",gap:"10px",alignItems:"center"}}>
                <div style={{flex:"1",fontSize:"16px",fontWeight:"500"}}>{headerText}</div>
                <div style={{color:"#00A6A4",fontSize:"12px",fontWeight:"500"}} onClick={()=>{setShowContent(!showContent)}}>{showContent?'Hide':'Show'}</div>
            </div>
            {showContent?
                <div>
                {
                    subHeaderArray.map((subHead)=>{
                        return <div style={{display:"flex",gap:'10px',marginTop:'3%'}}>
                            <img src={SmallBlueCube} />
                            <div style={{flex:"1",fontSize:"14px",fontWeight:"400"}}>{subHead}</div>
                        </div>
                    })
                }
                </div>
                :
                ''
            }
        </div>
    )
}

export default HideShowBox
