import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';

export default function CapabilityPage6(props) {

	const [healthInsurance, setHealthInsurance] = React.useState("0");

	const [loading, setLoading] = React.useState(false);

	const RadioInputOptions = [
		{ key: '0', name: 'Yes' },
		{ key: '1', name: 'No' }
	]

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setHealthInsurance(u.wealthCreator.healthInsurance || '0')
				}
			}
			setLoading(false);
		});
	}, []);

	return (
		<div>
			<MainHeader
				header="Risk taking capability"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={54} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textHead" style={{ fontWeight:"500" }}>Do you and all your dependents have sufficient health insurance cover?</div>
							{RadioInputOptions.map((obj) => (
								<div key={obj.key} style={{ display: 'flex', flexDirection: 'row' ,marginTop:"6%"}}>
									<div className="radio-item">
										<input
											type="radio"
											id={obj.key}
											name="ritem"
											value={obj.key}
											onChange={(val) => {
												setHealthInsurance(val.target.value)
												console.log(val.target.value)
											}}
											className="textNormal"
											checked={healthInsurance === obj.key}
										/>
										<label className="textNormal radio-label" htmlFor={obj.key}>{obj.name}</label>
									</div>
								</div>
							))}
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-capability-result"
				params={props.location.state}
				buttonText="Identify my risk taking capability"
				saveData={{
					wealthCreator: {
						healthInsurance
					}
				}}
			/>
		</div>
	)
}
