import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';

export default function CapabilityPage1(props) {

	const [age, setAge] = React.useState("");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "25", "name": "25 years" },
		{ "key": "30", "name": "30 years" },
		{ "key": "35", "name": "35 years" },
		{ "key": "40", "name": "40 years" }
	]

	const checkValidation = () => {
		if (age) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	},[age])

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setAge(u.wealthCreator.age || '')
				}
			}
			setLoading(false);
		});
	}, []);

	return (
		<div>
			<MainHeader
				header="Risk taking capability"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={9} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">Your present age</div>
							<input
								type="text"
								id="age"
								maxLength={2}
								className='normalInput'
								style={{color:"#294C88"}}
								placeholder='30 years'
								value={age}
								onChange={(num) => {
									const a = num.target.value.replace(/\D/g, '')
									setAge(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row',overflowX:'auto' }}>
								{chipset.map((item) => {
									return (
										<Chip
											className="chip-b"
											label={item.name}
											onClick={() => {
												setAge(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-capability-2"
				params={props.location.state}
				buttonText="Next"
				saveData={{
					wealthCreator: {
						age
					}
				}}
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
