import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import { ProgressBar } from 'react-bootstrap';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import cssVar from 'views/mobileView/styles';
import AdminServices from 'services/api/AdminServices';
const FatcaDetails = (props) => {
    const {taxStatus} = props.location.state
    const [income, setIncome] = React.useState('');
    const [wealth, setWealth] = React.useState('');
    const [occupation, setOccupation] = React.useState('');
    const [nextDisabled,setNextDisabled]=React.useState(false)

    const [loading,setloading] = React.useState();
    const [incomeList, setincomeList] = React.useState([]);
    const [wealthList, setwealthList] = React.useState([]);
    const [occupationList, setoccupationList] = React.useState([]);
    React.useEffect(async() => {
        setloading(true)
		await db.get(db.model.userData).then((u) => {
			if (u) {
				if(u.Fatca){
                    setIncome(u.Fatca.income || '')
                    setWealth(u.Fatca.wealth || '')
                    setOccupation(u.Fatca.occupation || '')
                    setoccupationList(u.Fatca.occupationList || []);
                    setincomeList(u.Fatca.incomeList || []);
                    setwealthList(u.Fatca.wealthList || []);
                } 
                else {
                    getData()
                }
			};
		})
        setloading(false)
	}, []);
    const getData = async () => {
        try {
            setloading(true)
            await AdminServices.getOccupation()
                .then(async (res) => {
                    const data = await dropdownCustom(res.data, ['UID', 'Occupation']);
                    setoccupationList(data);
                })
                .then(async () => {
                    await AdminServices.getAnnualIncome()
                        .then(async (res) => {
                            const data = await dropdownCustom(res.data, [
                                'UID',
                                'IncomeSlab'
                            ]);
                            setincomeList(data);
                        })
                        .then(async () => {
                            await AdminServices.getSourceofWealth()
                                .then(async (res) => {
                                    const data = await dropdownCustom(res.data, [
                                        'UID',
                                        'Source'
                                    ]);
                                    setwealthList(data);
                                })
                                .then(() => {
                                    setloading(false);
                                });
                        });
                });
        } catch (error) {
            console.log(error);
        }
    };
    React.useEffect(() => {
		checkValidation();
	}, [income,wealth,occupation]);
    const checkValidation = () => {
        if(income&&wealth&&occupation){
            setNextDisabled(false);
        }else {
            setNextDisabled(true);
        }
    };
    return (
        <div>
            <MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
            <ProgressBar now={60} className="progress-bar-custom" />
            <div className="containerData my-4" style={{padding:"4%"}}>
            {loading?
            <center>
                <Spinner
                as="span"
                animation="grow"
                size="sm"
                role="status"
                aria-hidden="true"
            />
            </center>
            :
            <div className="floating-form">
                <div className='textCardHeader'>
                    {taxStatus==='1'?'Select legal guardian income details':'Select income details'}
                </div>
                <CustomDropDown
                    label="Annual income"
                    initial={income}
                    items={incomeList}
                    autocomplete="off"
                    onChange={(item) => setIncome(item)} />
                <CustomDropDown
                    label="Source of wealth"
                    initial={wealth}
                    items={wealthList}
                    onChange={(item) => setWealth(item)} />
                <CustomDropDown
                    label="Occupation"
                    initial={occupation}
                    items={occupationList}
                    onChange={(item) => setOccupation(item)} />
            </div>
            }
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next={taxStatus==='1'?"/activate-direct-mf/bank-details":"/activate-direct-mf/nominee"}
                    params={{taxStatus:taxStatus}}
                    saveData={{
                        Fatca: {
                            income,
                           wealth,
                           occupation,
                           incomeList,
                           wealthList,
                           occupationList
                        }
                    }}/>
            </div>
        </div>
    )
}

export default FatcaDetails
