import React from 'react';
import db from 'libs/db';
import { setUserData } from 'redux/CompleteProfile/actions';
import cssVar from 'views/mobileView/styles';

const BottomBackNextBtn = ({
  next,
  navigation,
  params,
  nextDisabled=false,
  saveData,
  buttonText="Confirm"
}) => {

  const onNextPress = () => {
    if(nextDisabled){
      console.log("asasda",nextDisabled)
      return
    }
    if (saveData) {  
      db.get(db.model.userData).then((u) => {
        if (u) {
          Object.assign(u, saveData);
        } else {
          u = saveData;
        }
        console.log(u)
        setUserData(u);
        navigation.push({pathname: next, state : params})
      });
    } else {
        console.log(next,params)
        navigation.push({pathname: next, state : params})
    }
  };

  return (
    <button
      style={{ backgroundColor: cssVar.getPropertyValue('--brandSecondary'),
        borderRadius: "6px",
        color: "#fff",
        fontWeight: 500,
        height: "44px",
        flex: 1,
        width: "100%",
        opacity: nextDisabled ? 0.4 : 1 }}
      onClick={() => onNextPress()}
    >
      {buttonText}
    </button>
  );
};

export default BottomBackNextBtn;
