import db from 'libs/db';
import React from 'react';
import { Button, Spinner } from 'react-bootstrap';
import ReactSignatureCanvas from 'react-signature-canvas';
import { toast } from 'react-toastify';
import { ProgressBar } from 'react-bootstrap';
import AdminServices from 'services/api/AdminServices';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import ProfileCompleteHeader from 'views/mobileView/components/molecules/Headers/ProfileCompletionHeader';
import cssVar from 'views/mobileView/styles';
import cloudPng from 'assets/img/cloud.png';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';


const SignUpload = (props) => {

	const [imgSign, setImg] = React.useState('');
	const [signID, setsignID] = React.useState('');

	const [modalType, setmodalType] = React.useState('sign');
	const ref = React.useRef();
	const [ShowComment, setModelComment] = React.useState(false);
	const [animateModal, setanimateModal] = React.useState(true);
	const [loading, setloading] = React.useState(true);
	const [nextDisabled, setNextDisabled] = React.useState(true);
	const [user, setUser] = React.useState({});
	const [userProfile, setUserProfile] = React.useState({});

	var inputFile = React.useRef(null)
	var sigCanvas = React.useRef(null)



	//   function b64toBlob(b64Data, contentType) {
	//     contentType = contentType || '';
	//     sliceSize =  512;

	//     var byteCharacters = Base64.Base64.atob(b64Data);
	//     var byteArrays = [];

	//     for (var offset = 0; offset < byteCharacters.length; offset += sliceSize) {
	//         var slice = byteCharacters.slice(offset, offset + sliceSize);

	//         var byteNumbers = new Array(slice.length);
	//         for (var i = 0; i < slice.length; i++) {
	//             byteNumbers[i] = slice.charCodeAt(i);
	//         }

	//         var byteArray = new Uint8Array(byteNumbers);

	//         byteArrays.push(byteArray);
	//     }

	//   var blob = new Blob(byteArrays, {type: contentType});
	//   return blob;
	// }
	React.useEffect(() => {
		checkValidation();
	}, [signID]);

	function urltoFile(url, filename, mimeType) {
		return (fetch(url)
			.then(function (res) { return res.arrayBuffer(); })
			.then(function (buf) { return new File([buf], filename, { type: mimeType }); })
		);
	}

	let uploadImage = async (singleFile, modalType) => {
		//Check if any file is selected or not
		if (singleFile != null) {
			//If file selected then create FormData
			const fileToUpload = singleFile;
			const data = new FormData();
			data.append('file', fileToUpload);
			try {
				var res = await AdminServices.uploadDoc(user.id, data).then((res) => {
					return res;
				});
				console.log(res.data);
				if (res.type) {
					if (res.data.DocumentId) {
						setsignID(res.data.DocumentId);
					} else {
						console.log('ERR : 2', res.msg);
						toast.error("try again...")
					}
				} else {
					console.log('ERR : 1', res.msg);
					toast.error("try again...")

				}
			} catch (error) {
				console.log('ERR : c', error);
				toast.error("try again...")

			}
		} else {
			//if no file selected the show alert
			alert('Please Select File first');
		}
	};

	const imagePick = (event) => {
		// ImagePicker.openPicker({
		//   width: widthPercentageToDP('100%'),
		//   height: heightPercentageToDP('30%'),
		//   cropping: true
		// }).then((image) => {
		//   const file = {
		//     uri: image.path,
		//     name: 'pan.jpg',
		//     type: image.mime
		//   };
		//   uploadImage(file, 'sign');

		//   setImg(image.path);
		//   setModelComment(false);
		// });

		setImg(URL.createObjectURL(event.target.files[0]));
		var file = event.target.files[0];

		uploadImage(file, 'sign');
		setModelComment(false);
	};

	

	const handleSignature = () => {
		var a = sigCanvas.getTrimmedCanvas().toDataURL('image/png')
		console.log(a)
		urltoFile(a, 'sign.png', 'image/png')
			.then(function (file) { uploadImage(file, 'sign'); });
		
	};

	const handleClear = () => {
		console.log('clear success!');
		sigCanvas.clear()
		setsignID('')
		setImg('');
	};

	const checkValidation = () => {
		if (signID !== '' && signID !== null) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true);
		}
	};

	function getDateFinal(dateString) {
		console.log(dateString)
		dateString = dateString.split(" / ").reverse().join("-");
		return dateString;
	}

	const onNextPress = async () => {

		var final = {
			client: {
				clientId: user.id,
				clientName:  userProfile.name,
				mobileNumber: userProfile.taxStatus === '2' ? userProfile.PersonalDetails.phone : '',
				gender: userProfile.PersonalDetails.gender === '0' ? 1 : 2,
				dateOfBirth: new Date(getDateFinal(userProfile.PersonalDetails.dob)),
				pan: userProfile.taxStatus === '1' ? '' : userProfile.panData.pan,
				kycStatus: userProfile.taxStatus === '1'? userProfile.GuardianPan.kycRegistered : userProfile.panData.kycRegistered,
				taxStatus: userProfile.taxStatus === '0' ? 1 : userProfile.taxStatus === '1' ? 2 : userProfile.BankDetails.type,
				guardianPAN: userProfile.taxStatus === '1' ? userProfile.GuardianPan.pan : '',
				guardianName: userProfile.taxStatus === '1' ? userProfile.GuardianName : '',
				guardianDOB: userProfile.taxStatus === '1' ? new Date(getDateFinal(userProfile.GuardianName)) : new Date('0000-00-00'),
				panFileId: userProfile.doc.panID,
				bankFileId: userProfile.doc.chequeID,
				signFileId: signID
			},
			addresses: [
						{
							clientId: user.id,
							address1: userProfile.AddressDetails.line1,
							address2: userProfile.AddressDetails.line2,
							city: userProfile.AddressDetails.city,
							state: userProfile.AddressDetails.state,
							pincode: userProfile.AddressDetails.pincode,
							countryId: userProfile.AddressDetails.country.UID
						}
					],
			fatcas: [
					{
						clientId: user.id,
						pan: userProfile.taxStatus === '1' ? userProfile.GuardianPan.pan : userProfile.panData.pan,
						birthCountryId: 0,
						residentCountryId: 0,
						isTaxResident: true,
						politicalExposure: false,
						annualIncomeSlab: userProfile.Fatca.income.UID,
						sourceofWealth: userProfile.Fatca.wealth.UID,
						occupation: userProfile.Fatca.occupation.UID,
						identificationType: 0
					}
				],
			nominees: userProfile.taxStatus !== '1' && userProfile.Nominee?.addNominee==='0'?
				[{
					clientId: user.id,
					name: userProfile.Nominee.noName,
					relation: userProfile.Nominee.relation,
					dateOfBirth: new Date(getDateFinal(userProfile.Nominee.noDob)),
					allocation: 100,
					guardianName: userProfile.Nominee.noGuardian
				}]
				:
				[]
			,
			bankInfo: {
				clientId: user.id,
				bankId: userProfile.BankDetails.UID,
				bankName: userProfile.BankDetails.name,
				accountType: userProfile.BankDetails.type,
				accountNumber: userProfile.BankDetails.number,
				ifsc: userProfile.BankDetails.ifsc,
				isDefault: 1,
				isDeleted: 0
			}
		}
		console.log(final);
		try {
			var res = await AdminServices.completeprofile(final).then((res) => {
				return res;
			});
			console.log(res);
			if (res.type) {
				if (res.data.Result === 'success') {
					console.log(res);
					localStorage.setItem("needHomeReload", "yes")
					db.get(db.model.user,true).then((userData) => {
						console.log(userData)
						if (userData) {
							userData = {
								...userData.userData,
								profileComplete: true
							};
						} else {
							userData = {profileComplete: true};
						}
						db.set(db.model.user, JSON.stringify({ userData }),true);
						db.remove(db.model.userData);
						if((userProfile.taxStatus === '1'?userProfile.GuardianPan.kycRegistered : userProfile.panData.kycRegistered)==='1'){
							props.history.push('/activate-direct-mf/start-investing');
						}else{
							props.history.push('/activate-direct-mf/register-KYC');
						}
					});
				} else {
					console.log('ERR : 2', res.data);
					toast.error("Try Again...")
				}
			} else {
				console.log('ERR : 1', res.data);
				toast.error("Try Again...")
			}
		} catch (error) {
			console.log('ERR : c', error);
			toast.error("Try Again...")
		}
	};

	React.useEffect(() => {
		console.log(new Date('1111-11-11'))
		db.get(db.model.userData)
			.then((u) => {
				if (u) {
					setUserProfile(u);
					if (u.sign) {
						setImg(u.sign || '');
						setsignID(u.signID || '');
						u.sign.startsWith('data:')
							? setmodalType('sign')
							: setmodalType('pick');
					}
				}
			})
			.then(() => {
				db.get(db.model.user, true).then((p) => {
					setUser(p.userData);
					setloading(false);
				});
			});
	}, []);



	if (loading) {
		return (
			<div className="center-screen">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {

		return (
		<div>
			<MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
            <ProgressBar now={100} className="progress-bar-custom" />

			<div className="containerData my-4" style={{padding:"4%"}}>
				<div>
					{modalType === 'sign' ? (
						<>
							<div className="textCardHeader" style={{marginBottom:"6%"}}>Sign in the box below</div>
							<div
								style={{
									borderWidth: 1,
									display: 'flex',
									borderStyle: 'dashed',
									borderRadius: 5,
									justifyContent: 'space-between',
								}}>
								<ReactSignatureCanvas ref={(ref) => {
									sigCanvas = ref
								}} penColor={cssVar.getPropertyValue("--color-text")} backgroundColor={cssVar.getPropertyValue("--bg")}
									canvasProps={{ width: window.innerWidth - 33, height: 220, className: 'sigCanvas' }} />
								<p
									style={
										{
											position: 'absolute',
											top: 230,
											right: window.innerWidth / 2.4,
											opacity: 0.5
										}
									}>
									Sign here
								</p>
								<div style={
									{
										position: 'absolute',
										top: 315,
										alignContent: "center",
										right: window.innerWidth / 3.3,
									}
								}>
									<Button style={{ backgroundColor: cssVar.getPropertyValue("--brandSecondary"), borderColor: cssVar.getPropertyValue("--brandSecondary") }} onClick={() => handleSignature()} className="mx-2">Save</Button>
									<Button style={{ backgroundColor: cssVar.getPropertyValue("--brandSecondary"), borderColor: cssVar.getPropertyValue("--brandSecondary") }} onClick={() => handleClear()} className="mx-2">Clear</Button>

								</div>
							</div>
						</>
					) :
						(
							<div >
								<div className="textCardHeader" style={{marginBottom:"6%"}}>
									Upload signature
								</div>
								{imgSign === '' || imgSign === null ? (

									<div
										style={{
											borderWidth: 1,
											borderStyle: 'dashed',
											borderColor: cssVar.getPropertyValue("--primary"),
											borderRadius: 10,
											backgroundColor: cssVar.getPropertyValue("--cardColor"),
											alignItems: 'center',
											padding: "30px",
											display: "flex",
											justifyContent: "space-around",
											marginTop: 20
										}}
										onClick={() => {
											inputFile.current.click();
											setModelComment(true);
										}}>
										<>
											<input type='file' accept="image/*" id='panfile' ref={inputFile} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

											<span
												style={{

													color: cssVar.getPropertyValue("--primary")
												}}>
												Take a photo or upload an image
											</span>
											<img
												src={cloudPng} />
										</>
									</div>
								) : (
									<div
										style={{
											borderWidth: 1,
											borderStyle: 'dashed',
											borderColor: cssVar.getPropertyValue("--primary"),
											borderRadius: 10,
											backgroundColor: cssVar.getPropertyValue("--cardColor"),
											display: "flex",
											flexDirection: "column",
											justifyContent: "space-around",
											height: "10px !important",


										}}>
										<img
											width="100%" height="160"

											style={{
												borderRadius: 10
											}}
											src={imgSign}

										/>
										<div
											style={{
												alignItems: 'center',
												padding: "10px",
												display: "flex",
												justifyContent: "space-around"
											}}
											onClick={() => {
												inputFile.current.click();
												setModelComment(true);
												setmodalType('pan');
											}}>
											<>
												<input type='file' accept="image/*" id='panfile' ref={inputFile} style={{ display: 'none' }} onChange={(event) => imagePick(event)} />

												<span
													style={{
														color: cssVar.getPropertyValue("--primary")
													}}>
													Upload a different image
												</span>
												<img

													resizeMode="contain"
													src={cloudPng} />
											</>
										</div>
									</div>
								)}
							</div>)}


					{modalType === 'sign' ? (
						<p className="textNormal mt-3"
							style={{ color: cssVar.getPropertyValue("--primary") }}

							onClick={() => {
								setmodalType('pick');
								setModelComment(true);
								setImg('');
								setsignID('')
							}}
						>
							Upload signature instead
						</p>
					) : (
						<p className="textNormal mt-3"
							style={{ color: cssVar.getPropertyValue("--primary") }}
							onClick={() => {
								setmodalType('sign');
								setModelComment(false);
								setImg('');
								setsignID('')

							}}
						>
							Sign it instead
						</p>
					)}
				</div>
			</div>
			<div className="footer">
					<button
						style={{ backgroundColor: cssVar.getPropertyValue('--brandSecondary'),
						borderRadius: "6px",
						color: "#fff",
						fontWeight: 500,
						height: "44px",
						flex: 1,
						width: "100%",
						opacity: nextDisabled ? 0.4 : 1 }}
						onClick={!nextDisabled
							? () => onNextPress()
							: () => { }}
					>
						Activate mutual funds now
					</button>
				</div>
		</div>
		);


	}
};

export default SignUpload;
