import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';

export default function CapabilityPageResult(props) {

	const [age, setAge] = React.useState("");
	const [dependents, setDependents] = React.useState("");
	const [income, setIncome] = React.useState("");
	const [emergencyFund, setEmergencyFund] = React.useState("");
	const [lifeInsurance, setLifeInsurance] = React.useState("");
	const [healthInsurance, setHealthInsurance] = React.useState("0");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setAge(u.wealthCreator.age || '')
					setDependents(u.wealthCreator.dependents || '')
					setIncome(u.wealthCreator.income || '')
					setEmergencyFund(u.wealthCreator.emergencyFund || '')
					setLifeInsurance(u.wealthCreator.lifeInsurance || '')
					setHealthInsurance(u.wealthCreator.healthInsurance || '0')
				}
			}
			setLoading(false);
			setNextDisabled(false)
		});
	}, []);



	return (
		<div>
			<MainHeader
				header="Risk taking capability"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={63} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
						  <div className='textHead' style={{fontWeight:"500"}}>We have identified your risk taking capability as high</div>
						  <div className='textHead' style={{marginTop:"6%"}}>Please continue to identify your risk taking willingness now for full risk analysis</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-willingness-1"
				params={props.location.state}
				buttonText="Continue"
				saveData={{
				}}
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
