import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import { ProgressBar } from 'react-bootstrap';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import cssVar from 'views/mobileView/styles';
import AdminServices from 'services/api/AdminServices';
import DOBInputMask from 'views/mobileView/components/atoms/DOBInputMask';
import { Chip, Stack } from '@mui/material';

const NomineeDetails = (props) => {
	const { taxStatus } = props.location.state
	const [addNominee, setAddNominee] = React.useState('1')
	const [noName, setNoName] = React.useState('')
	const [noRelation, setNoRelation] = React.useState('')
	const [noDob, setNoDob] = React.useState('')
	const [noGuardian, setNoGuardian] = React.useState('')

	const [nextDisabled, setNextDisabled] = React.useState(false)
	const [loading, setLoading] = React.useState(true);
	const data = [
		{ key: '0', value: 'Yes' },
		{ key: '1', value: 'No' }
	]
	const chipset = [
		{ "key": "a", "name": "Father" },
		{ "key": "b", "name": "Mother" },
		{ "key": "c", "name": "Spouse" },
		{ "key": "d", "name": "Son" },
		{ "key": "e", "name": "Daughter" },
	]
	React.useEffect(() => {
		getData()
		db.get(db.model.userData).then((u) => {
			if (u) {
				if (u.Nominee) {
					setAddNominee(u.Nominee.addNominee || '')
					setNoName(u.Nominee.noName || '')
					setNoRelation(u.Nominee.noRelation || '')
					setNoDob(u.Nominee.noDob || '')
					setNoGuardian(u.Nominee.noGuardian || '')
				}
			};
			setLoading(false)
		})
	}, []);
	const getData = async () => {

	};
	React.useEffect(() => {
		checkValidation();
	}, [addNominee, noName, noRelation, noDob, noGuardian]);

	const checkValidation = () => {
		setNextDisabled(true)
		if (addNominee === '1') {
			setNextDisabled(false);
		} else {
			if (noName.length > 2 && noRelation !== '' && noDob.length === 14) {
				if (getAge(noDob) < 18 && noGuardian !== '') {
					setNextDisabled(false);
				} else if (getAge(noDob) > 18) {
					setNextDisabled(false);
				}
			}
		}
	};
	function getAge(dateString) {
		dateString = dateString.split(" / ").reverse().join("/");
		var today = new Date();
		var birthDate = new Date(dateString);
		var age = today.getFullYear() - birthDate.getFullYear();
		var m = today.getMonth() - birthDate.getMonth();
		if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
			age--;
		}
		return age;
	}
	return (
		<div>
			<MainHeader
				header="Activate direct mutual funds"
				right="help"
				screenType="ProfileCompletion"
				navigation={props.history}
			/>
			<ProgressBar now={70} className="progress-bar-custom" />
			<div className="containerData my-4" style={{ padding: "4%" }}>
				<div className='textCardHeader'>
					Do you wish to add a nominee?
				</div>
				{data.map((obj) => (
					<div key={obj.key} style={{ display: 'flex', flexDirection: 'row', marginTop: "6%" }}>
						<div className="radio-item">
							<input
								type="radio"
								id={obj.key}
								name="ritem"
								value={obj.key}
								onChange={(val) => {
									setAddNominee(val.target.value)
									console.log(val.target.value)
								}}
								className="textNormal"
								checked={addNominee === obj.key}
							/>
							<label className="textNormal radio-label" htmlFor={obj.key}>{obj.value}</label>
						</div>
					</div>
				))}
				{addNominee === '0' ?
					<>
						<div className='textCardHeader' style={{marginTop:"6%"}}>Enter nominee details</div>
						<FloatingTextInput
							type="text"
							id="full name"
							label="Full name"
							value={noName}
							onChange={(text) => {
								setNoName(text.target.value)
								console.log(text.target.value)
							}}
						/>

						<FloatingTextInput
							value={noRelation}
							type="text"
							autoCapitalize="words"
							id="relation"
							label="Relationship with you"
							onBlur={() => checkValidation()}
							onChange={(text) => {
								setNoRelation(text.target.value)
							}}
						/>
						<div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between', overflow: "scroll" }}>
							{chipset.map((item) => {
								return (
									<Stack direction="row" spacing={1} key={item.name}>
										<Chip
											className="chip-b"
											label={item.name}
											variant="outlined"
											onClick={() => {
												setNoRelation(item.name)
											}} />
									</Stack>
								)
							})}
						</div>
						{loading ? "" :
							<DOBInputMask
								dateCheck
								label="Date of birth"
								floatingLabel={true}
								defaultVal={noDob}
								dobof={props.location.state.selection}
								minAge={0}
								maxAge={200}
								dateValidation={(dobvv) => { }}
								setDOB={(dobb) => {
									setNoDob(dobb);
								}}
								onBlur={() => checkValidation()}
							/>}
						{getAge(noDob) < 19 ?
							<FloatingTextInput
								type="text"
								id="guard name"
								autoCapitalize="words"
								label="Legal guardian name"
								onBlur={() => checkValidation()}
								value={noGuardian}
								onChange={(text) => {
									setNoGuardian(text.target.value)
								}}
							/> : null}
					</>
					:
					""
				}
			</div>
			<div className='footer'>
				<BottomBackNextBtn
					nextDisabled={nextDisabled}
					navigation={props.history}
					buttonText="Confirm"
					next="/activate-direct-mf/bank-details"
					params={{ taxStatus: taxStatus }}
					saveData={{
						Nominee: {
							addNominee,
							noName,
							noRelation,
							noDob,
							noGuardian,
						}
					}} />
			</div>
		</div>
	)
}

export default NomineeDetails
