import React from 'react';
// import { CurrencyFormat } from '../../atoms/CurrencyFormat';
import cssVar from "views/mobileView/styles";
import { CurrencyFormat } from '../../atoms/CurrencyFormat';

const HomePortfolioCard = ({ header, navigation, data, clickable=true }) => {

  return (
    <div onClick={() => clickable ?   navigation.push('/Portfolio')  : {}} style={{ position: 'relative' }}>
      <div style={{ flex: 1, borderRadius: 5, height: 'auto', margin: "4%" }}>
        <div className="mt-2" style={{ fontSize: cssVar.getPropertyValue("--font-normal") }}>
          {header}
        </div>

        <div style={{ fontSize: cssVar.getPropertyValue("--font-numHead"), justifyContent: "center" }}>
          {<CurrencyFormat num={data[0]} type="2" />} <span style={{ color: cssVar.getPropertyValue("--brandSecondary"), fontSize: "15px" }}><i class="fa fa-refresh"></i>

          </span>
        </div>
        {!data[1].includes('-') ? (
          <div style={{ color: cssVar.getPropertyValue("--primary"), fontSize: 12 }}>
            <span style={{ color: cssVar.getPropertyValue("--textLight"), fontSize: 12 }}>1 Day </span>
            &#x25B2; {<CurrencyFormat num={data[2]} />} ({`${Number(data[1]).toFixed(2)}%`})
          </div>
        ) : (
          <div style={{ color: 'red' }}>
            &#x25BC; {data[2]} ({`${Number(data[1]).toFixed(2)}%`})
          </div>
        )}


        <div style={{ display: "flex", flexDirection: "row", justifyContent: "space-between", marginTop: 20, paddingBottom: 20 }}>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <span style={{ fontSize: 12,  color: cssVar.getPropertyValue("--textLight"), textAlign: 'left' }}>
              Invested
            </span>
            <span style={{ fontSize: 14,  textAlign: 'left', color: cssVar.getPropertyValue("--text") }}>
              <CurrencyFormat num={data[3]} type="2" />
            </span>
          </div>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <span style={{ fontSize: 12,  color: cssVar.getPropertyValue("--textLight"), textAlign: 'left' }}>
              Abs. return
            </span>
            <span style={{ fontSize: 14,  color: !(data[4]).toString().includes('-') ? cssVar.getPropertyValue("--primary") : cssVar.getPropertyValue("--red"), textAlign: 'left' }}>
              {`${Number(data[4]).toFixed(2)}%`}
            </span>
          </div>
          <div style={{ display: "flex", flexDirection: "column" }}>
            <span style={{ fontSize: 12,  color: cssVar.getPropertyValue("--textLight"), textAlign: 'left' }}>
              Ann. return
            </span>
            <span style={{ fontSize: 14,  color: !(data[5]).toString().includes('-') ? cssVar.getPropertyValue("--primary") : cssVar.getPropertyValue("--red"), textAlign: 'left' }}>
              {`${Number(data[5]).toFixed(2)}%`}
            </span>
          </div>




        </div>
      </div>
    </div>
  );
};

export default HomePortfolioCard;
