	import React from 'react';
	import { Spinner } from 'react-bootstrap';
	import Footer from './Footer';
	import db from 'libs/db';
	import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
	import { ProgressBar } from 'react-bootstrap';
	
	export default function InvestmentHorizonWC(props) {
	
		const [investmentHorizon, setInvestmentHorizon] = React.useState("0");
	
		const [loading, setLoading] = React.useState(false);
	
		const RadioInputOptions = [
			{ key: '0', name: '5-7 years' },
			{ key: '1', name: '7-10 years' },
			{ key: '2', name: '10+ years' },
		]
	
		React.useEffect(() => {
			setLoading(true)
			db.get(db.model.userData).then((u) => {
				console.log(u)
				if (u) {
					if (u.wealthCreator) {
						setInvestmentHorizon(u.wealthCreator.investmentHorizon || '0')
					}
				}
				setLoading(false);
			});
		}, []);
	
		return (
			<div>
				<MainHeader
					header="Investment horizon"
					right=""
					screenType="WealthCreator"
					navigation={props.history}
				/>
				<ProgressBar now={100} className="progress-bar-custom" />
				<div className='containerData my-4'>
					{
						loading ?
							<center>
								<Spinner
									as="span"
									animation="grow"
									size="sm"
									role="status"
									aria-hidden="true" />
							</center>
							:
							<>
								<div className="textHead" style={{ fontWeight:"500" }}>Select your investment horizon</div>
								{RadioInputOptions.map((obj) => (
									<div key={obj.key} style={{ display: 'flex', flexDirection: 'row',marginTop:"6%" }}>
										<div className="radio-item">
											<input
												type="radio"
												id={obj.key}
												name="ritem"
												value={obj.key}
												onChange={(val) => {
													setInvestmentHorizon(val.target.value)
													console.log(val.target.value)
												}}
												className="textNormal"
												checked={investmentHorizon === obj.key}
											/>
											<label className="textNormal radio-label" htmlFor={obj.key}>{obj.name}</label>
										</div>
									</div>
								))}
							</>
					}
				</div>
				<Footer
					navigation={props.history}
					next="/wealth-creator/your-wealth-creator"
					params={props.location.state}
					buttonText="Select your investment horizon"
					saveData={{
						wealthCreator: {
							investmentHorizon
						}
					}}
				/>
			</div>
		)
	}
	