import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import { ProgressBar } from 'react-bootstrap';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
import cssVar from 'views/mobileView/styles';
import DOBInputMask from 'views/mobileView/components/atoms/DOBInputMask';
const PersonalDetails = (props) => {
    const {taxStatus} = props.location.state
    const [dob,setDob]=React.useState('');
    const [gender,setGender]=React.useState('0');
    const [phone,setPhone]=React.useState('');
    const [dobv, setDobv] = React.useState('');
    const [nextDisabled,setNextDisabled]=React.useState(false)
    const [loading,setLoading]=React.useState(true);
    const data = [
        { key: '0', gender: 'Male' },
        { key: '1', gender: 'Female' }
    ]
    React.useEffect(async() => {
		await db.get(db.model.userData).then((u) => {
			if (u) {
				if(u.PersonalDetails){
                    setDob(u.PersonalDetails.dob || '')
                    console.log("setDob",u.PersonalDetails.dob)
                    setGender(u.PersonalDetails.gender || '0')
                    setPhone(u.PersonalDetails.phone || '')
                }
			};
            setLoading(false)
		})
	}, []);

    React.useEffect(() => {
		checkValidation();
	}, [dob,gender,phone,dobv]);

    const checkValidation = () => {
        if(taxStatus==='2'){
            if(phone.length===10 && !dobv && dob.length===14){
                setNextDisabled(false)
            } else {
                setNextDisabled(true)
            }
        } else {
            if(!dobv && dob.length===14 ){
                setNextDisabled(false)
            } else {
                setNextDisabled(true)
            }
        }
    };
    return (
        <div>
            <MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
            <ProgressBar now={40} className="progress-bar-custom" />
            <div className="containerData my-4" style={{padding:"4%"}}>
                <div className="textNormal">{taxStatus==='1'?"Enter minor investor date of birth":"Enter your date of birth"}</div>
                {loading?'':<DOBInputMask
                        dateCheck
                        floatingLabel={false}
                        onBlur={() => console.log("hey")}
                        placeholder="01/01/1990"
                        defaultVal={dob}
                        minAge={taxStatus==='1' ? 1 : 18}
                        maxAge={taxStatus==='1' ? 18 : 200}
                        dateValidation={(dobvv) => {
                            setDobv(dobvv)
                            // console.log(dobvv)
                        }}
                        setDOB={(dobb) => {
                            setDob(dobb);
                            console.log(dobb)
                        }}
                    />}
                <div className='textHead' style={{fontWeight:"500",marginTop:"6%"}}>{taxStatus==='1'?"Select minor investor gender":"Select your gender"}</div>
                {data.map((obj) => (
                    <div key={obj.key} style={{ display: 'flex', flexDirection: 'row',marginTop:"6%" }}>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id={obj.key}
                                name="ritem"
                                value={obj.key}
                                onChange={(val) => {
                                    setGender(val.target.value)
                                    console.log(val.target.value)
                                }}
                                className="textNormal"
                                checked={gender === obj.key}
                            />
                            <label className="textNormal radio-label" htmlFor={obj.key}>{obj.gender}</label>
                        </div>
                    </div>
                ))}

                {taxStatus === '2' ? (
                    <FloatingTextInput
                        type="text"
                        label="Mobile number"
                        id="Phone-number"
                        placeholder="10 digit Indian mobile number"
                        autoCapitalize="words"
                        maxLength={10}
                        value={phone}
                        onChange={(num) => {
                            const newPhone = num.target.value.replace(/\D/g, '');
                            setPhone(newPhone);
                            console.log(num.target.value)
                        }}
                    />
                ) : null}
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next={taxStatus==='1'?"/activate-direct-mf/guardian-pan":"/activate-direct-mf/address-details"}
                    params={{taxStatus:taxStatus}}
                    saveData={{
                        PersonalDetails:{
                            dob,
                            gender,
                            phone
                        }
                    }}/>
            </div>
        </div>
    )
}

export default PersonalDetails
