import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';

export default function CapabilityPage5(props) {

	const [lifeInsurance, setLifeInsurance] = React.useState("");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "0", "name": "No life insurance" },
		{ "key": "10000000", "name": "1 Crore" },
		{ "key": "20000000", "name": "2 Crore" },
		{ "key": "30000000", "name": "3 Crore" },
	]

	const checkValidation = () => {
		if (lifeInsurance) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [lifeInsurance])

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setLifeInsurance(u.wealthCreator.lifeInsurance || '')
				}
			}
			setLoading(false);
		});
	}, []);

	return (
		<div>
			<MainHeader
				header="Risk taking capability"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={45} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textHead">Life insurance cover</div>
							<input
								type="text"
								className='normalInput'
								style={{color:"#294C88"}}
								placeholder='1,00,00,000'
								id="lifeInsurance"
								value={lifeInsurance}
								onChange={(num) => {
									const a = num.target.value.replace(/\D/g, '')
									setLifeInsurance(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row',overflowX:'auto' }}>
								{chipset.map((item) => {
									return (
										<Chip
											className="chip-b"
											label={item.name}
											onClick={() => {
												setLifeInsurance(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-capability-6"
				params={props.location.state}
				buttonText="Next"
				saveData={{
					wealthCreator: {
						lifeInsurance
					}
				}}
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
