import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import { ProgressBar } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import cssVar from 'views/mobileView/styles';
import AdminServices from 'services/api/AdminServices';
const AddressDetails = (props) => {
    const { taxStatus } = props.location.state
    const [showDetails, setShowDetails] = React.useState(false);
    const [line1, setLine1] = React.useState('');
    const [line2, setLine2] = React.useState('');
    const [pincode, setPincode] = React.useState('');
    const [pincodeError, setPincodeError] = React.useState('');
    const [city, setCity] = React.useState('');
    const [state, setState] = React.useState('');
    const [country, setCountry] = React.useState({});
    const [countryList, setCountryList] = React.useState({});
    const [nextDisabled, setNextDisabled] = React.useState(false)
    const [loader, setLoader] = React.useState(false);
    React.useEffect(async () => {
        setLoader(true);
        await getData();
        await db.get(db.model.userData).then((u) => {
            if (u) {
                if (u.AddressDetails) {
                    setLine1(u.AddressDetails.line1 || '')
                    setLine2(u.AddressDetails.line2 || '')
                    setPincode(u.AddressDetails.pincode || '')
                    setCity(u.AddressDetails.city || '')
                    setState(u.AddressDetails.state || '')
                    setCountry(u.AddressDetails.country || '')
                }
            };
        })
        setLoader(false);
    }, []);
    React.useEffect(() => {
        checkValidation();
    }, [line1, line2, pincode, city, state, country]);

    React.useEffect(async () => {
        if (taxStatus === '2') {
            return
        }
        setShowDetails(false)
        setCity('')
        setState('')
        setCountry('')
        if (pincode.length === 6) {
            setLoader(true);
            const res = await AdminServices.verifyZip(pincode).then((res) => res);
            if (res.type) {
                if (res.data.Result === 'success') {
                    console.log(res.data)
                    setCity(res.data.City)
                    setState(res.data.State)
                    setCountry({ UID: 106 })
                    setShowDetails(true)
                    setPincodeError('')
                    setLoader(false);
                } else {
                    setPincodeError("Invalid pincode");
                    setLoader(false);
                }
            } else {
                setPincodeError("Invalid pincode");
                setLoader(false);
            }
        }
    }, [pincode]);


    const getData = async () => {
        const res = await AdminServices.getCountries().then((res) => res);
        // console.log('DATA : ', res);
        const data = await dropdownCustom(res.data, ['UID', 'Country']);
        console.log('sd', data);
        setCountryList(data);
    };
    const checkValidation = () => {
        console.log(country)
        if (line1.length > 0 && pincode.length === 6 && city?.length > 0 && state?.length > 0 && country !== '') {
            setNextDisabled(false)
        } else {
            setNextDisabled(true)
        }
    };
    return (
        <div>
            <MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
            <ProgressBar now={50} className="progress-bar-custom" />
            <div className="containerData my-4" style={{ padding: "4%" }}>
                <div className='textCardHeader'>
                    {taxStatus === '0' ? 'Enter your address' : ''}
                    {taxStatus === '1' ? 'Enter legal guardian address' : ''}
                    {taxStatus === '2' ? 'Enter your overseas address' : ''}
                </div>
                <FloatingTextInput
                    type="text"
                    id="line1"
                    label="Line 1"
                    onChange={(val) => {
                        setLine1(val.target.value)
                    }}
                    autoCapitalize="words"
                    value={line1}
                />

                <FloatingTextInput
                    type="text"
                    id="line2"
                    label="Line 2"
                    autoCapitalize="words"
                    value={line2}
                    onChange={(val) => {
                        setLine2(val.target.value)
                        console.log(val.target.value)
                    }}
                />

                <FloatingTextInput
                    type="tel"
                    id="pin"
                    label="PIN code"
                    maxLength="6"
                    // error={helperType === 'pin'}
                    value={pincode}
                    onChange={(val) => {
                        setPincode(val.target.value)
                        console.log(val.target.value)
                    }}
                />
                {loader ?
                    <center>
                        <Spinner
                            as="span"
                            animation="grow"
                            size="sm"
                            role="status"
                            aria-hidden="true"
                        />
                    </center>
                    :
                    <>
                        {pincodeError !== '' ? <div style={{ color: "red", fontSize: "12px" }}>{pincodeError}</div> : ""}
                        {taxStatus !== '2' && showDetails ?
                            <>
                                <div style={{ marginTop: "5%", fontSize: "14px" }}>City: <b>{city}</b></div>
                                <div style={{ marginTop: "5%", fontSize: "14px" }}>State: <b>{state}</b></div>
                                <div style={{ marginTop: "5%", fontSize: "14px" }}>Country: <b>India</b></div>
                            </>
                            :
                            ""
                        }
                    </>}
                {taxStatus === '2' ?
                    <>
                        <FloatingTextInput
                            type="text"
                            id="city"
                            label="City"
                            autoCapitalize="words"
                            value={city}
                            onChange={(val) => {
                                setCity(val.target.value)
                            }}
                        />
                        <FloatingTextInput
                            type="text"
                            id="state"
                            label="State/Province"
                            autoCapitalize="words"
                            value={state}
                            onChange={(val) => {
                                setState(val.target.value)
                            }}
                        />
                        {
                            loader?
                            ''
                            :
                            <CustomDropDown
                            placeholder="Enter country name"
                            label="Country"
                            initial={country}
                            items={countryList}
                            onChange={(item) => setCountry(item)} />
                        }
                    </>
                    :
                    ""
                }
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next="/activate-direct-mf/fatca"
                    params={{ taxStatus: taxStatus }}
                    saveData={{
                        AddressDetails: {
                            line1,
                            line2,
                            pincode,
                            city,
                            state,
                            country
                        }
                    }} />
            </div>
        </div>
    )
}

export default AddressDetails
