import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import { ProgressBar } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
const GuardianName = (props) => {
    const {taxStatus} = props.location.state
    const [name,setName]=React.useState('');
    const [nextDisabled,setNextDisabled]=React.useState(false)

    React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
			if (u) {
                    setName(u.GuardianName || '')
			};
		})
	}, []);
    React.useEffect(() => {
		checkValidation();
	}, [name]);
    const checkValidation = () => {
            if (name.length >= 2 ) {
                setNextDisabled(false);
            } else {
                setNextDisabled(true);
            }  
    };

    return (
        <div>
            <MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
            <ProgressBar now={45} className="progress-bar-custom" />
            <div className="containerData my-4" style={{padding:"4%"}}>
                <div className="textNormal">Enter legal guardian name (as per PAN card)</div>
                <input
                    className="capitalize normalInput"
                    placeholder="Vijay Sharma"
                    type="text"
                    id="Full-name"
                    value={name}
                    onChange={(text) => {
                        const newName = text.target.value.replace(/[^a-zA-Z\s]/g, '');
                        setName(newName);
                    }}
                />
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next="/activate-direct-mf/guardian-DOB"
                    params={{taxStatus:taxStatus}}
                    saveData={{
                        GuardianName:name
                    }}/>
            </div>
        </div>
    )
}

export default GuardianName
