import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import { ProgressBar } from 'react-bootstrap';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
import { dropdownCustom } from 'views/mobileView/components/atoms/DataCustomizer';
import cssVar from 'views/mobileView/styles';
import AdminServices from 'services/api/AdminServices';
import {ifscValidator} from 'libs/validators';
const BankDetails = (props) => {
    const {taxStatus} = props.location.state
    const data = taxStatus==='2'?[
        { key: '3', value: 'NRE' },
        { key: '4', value: 'NRO' }
    ]:[
        { key: '0', value: 'Savings' },
        { key: '1', value: 'Current' }
    ];
    const [UID,setUID]=React.useState('')
    const [type,setType]=React.useState(taxStatus==='2'?'3':'0')
    const [number,setNumber]=React.useState('')
    const [ifsc,setIfsc]=React.useState('')
    const [ifscError,setIfscError]=React.useState(false);
    const [name,setName] =React.useState('')
    const [bankList, setbankList] = React.useState([]);

    const [nextDisabled,setNextDisabled]=React.useState(false)
    const [loader,setLoader] = React.useState();
    
    React.useEffect(async() => {
        await getData()
		db.get(db.model.userData).then((u) => {
			if (u) {
				if(u.BankDetails){
                    setUID(u.BankDetails.UID || '')
                    setType(u.BankDetails.type)
                    setNumber(u.BankDetails.number || '')
                    setIfsc(u.BankDetails.ifsc || '')
                    setName(u.BankDetails.name || '')
                }
			};
		})
	}, []);
    const getData = async () => {
        const res = await AdminServices.getBanks().then((res) => res);
		const data = await dropdownCustom(res.data, ['UID', 'Name']);
		setbankList(data);
    };

    React.useEffect(() => {
		checkValidation();
	}, [number,ifsc,ifscError,name]);

    React.useEffect(async() => {
        if(ifscValidator(ifsc).isValid && ifsc.length===11){
            try {
                setLoader(true);
                const res = await AdminServices.verifyifsc(ifsc).then((res) => res);
                if (res.type) {
                    const a = JSON.parse(res.data);
                    console.log(a)
                    if (a !== 'Not Found') {
                        var bName = bankList.filter(data => data.name.includes(a.BANK))[0];
                        console.log(bName)
                        setIfscError(false)
                        setUID(bName.UID)
                        setName(bName.Name)
                    } else {
                        setIfscError(true)
                        setName('');
                    }
                    setLoader(false);
                } else {
                    console.log('ERR : 1', res.msg);
                    setIfscError(true)
                    setName('');
                    setLoader(false);
                }
            } catch (error) {
                console.log('ERR : c', error);
                setIfscError(true)
                setName('');
                setLoader(false);
            }
        } else{
            setName('');
        }
	}, [ifsc]);

    const checkValidation = () => {
        if(number.length>0 && ifscValidator(ifsc).isValid && !ifscError && name!==''){
            setNextDisabled(false)
            return true
        } else {
            setNextDisabled(true)
            return false
        }
       
    };

    return (
        <div>
            <MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
            <ProgressBar now={80} className="progress-bar-custom" />
            <div className="containerData my-4" style={{padding:"4%"}}>
            <div className='textCardHeader'>
                Enter bank account details
                {taxStatus==='1'?<div style={{fontSize:"12px",fontWeight:"400",color:"#6c6c6c"}}>Only bank accounts that have both the legal guardian and minor investor as joint holders are allowed</div>:""}
            </div>
            <div style={{fontSize:"14px",fontWeight:"500",marginTop:'6%'}}>Select account type</div>
            {data.map((obj) => (
                    <div key={obj.key} style={{ display: 'flex', flexDirection: 'row',marginTop:"6%" }}>
                        <div className="radio-item">
                            <input
                                type="radio"
                                id={obj.key}
                                name="ritem"
                                value={obj.key}
                                onChange={(val) => {
                                    setType(val.target.value)
                                    console.log(val.target.value)
                                }}
                                className="textNormal"
                                checked={type === obj.key}
                            />
                            <label className="textNormal radio-label" htmlFor={obj.key}>{obj.value}</label>
                        </div>
                    </div>
                ))}
                <FloatingTextInput
                    value={number}
                    label="Account number"
                    autoCapitalize="characters"
                    onChange={(text) => setNumber(text.target.value)}
                />
                <FloatingTextInput
                    value={ifsc}
                    className="uppercase"
                    label="IFSC"
                    autoCapitalize="characters"
                    onChange={(text) => setIfsc(text.target.value)}
                />
                {loader ? 
							<center>
								<Spinner
									as="span"
									animation="grow"
									size="sm"
									role="status"
									aria-hidden="true"
								/>
							</center>
						:
                    <>
                {ifscError?<span style={{color:"red", fontSize:"12px"}}>Invalid IFSC</span>:""}
                {name!==''?<div style={{marginTop:"4%", fontSize:"14px"}}>Bank : <b>{name}</b></div>:""}
                </>}
            </div>
            <div className='footer'>
                <BottomBackNextBtn
                    nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next="/activate-direct-mf/document-upload"
                    params={{taxStatus:taxStatus}}
                    saveData={{
                        BankDetails: {
                            UID,
                            type,
                            number,
                            ifsc,
                            name
                        }
                    }}/>
            </div>
        </div>
    )
}

export default BankDetails
