import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { Spinner } from 'react-bootstrap';
import { ProgressBar } from 'react-bootstrap';
import FloatingTextInput from 'views/mobileView/components/atoms/FloatingTextInput';
import CustomDropDown from 'views/mobileView/components/molecules/DropDown/CustomDropDown';
import BottomBackNextBtn from 'views/mobileView/components/atoms/BottomBackNextBtn';
import db from 'libs/db';
import { AiFillCheckCircle,AiFillCloseCircle } from 'react-icons/ai';
import AdminServices from 'services/api/AdminServices';

const GuardianPan = (props) => {
    const {taxStatus} = props.location.state
    const [pan,setPan]=React.useState('');
    const [nameOnPan,setNameOnPan]=React.useState('');
	const [kycRegistered,setKycRegistered]=React.useState('0');
    const [nextDisabled,setNextDisabled]=React.useState(true);
	const [loader,setloader]=React.useState(false);
    React.useEffect(() => {
		db.get(db.model.userData).then((u) => {
			if (u) {
				if(u.GuardianPan){
                    setPan(u.GuardianPan.pan || '')
                }
			};
		})
	}, []);

	React.useEffect(() => {
		setNextDisabled(true);
		if(pan.length===10){
			checkKYC(pan)
		}
	}, [pan]);
	const checkKYC = async (pan) => {
		setloader(true);
		
		var data = {
			pan: pan
		}
		try {
			const res = await AdminServices.checkkyc(data).then((res) => res);
			if (res.type) {
				if (res.data.status === '1') {
					setNameOnPan(res.data.f_PAN_NAME);
					setKycRegistered('1')
				} else {
					setNameOnPan('');
					setKycRegistered('0')
				}
				setNextDisabled(false)
				setloader(false);
			} else {
				toast.error("Try again ...");
				setloader(false);
			}
		} catch (error) {
			toast.error("Try again ...");
			setloader(false);
		}
	};

	
    return (
        <div>
            <MainHeader
                header="Activate direct mutual funds"
                right="help"
                screenType="ProfileCompletion"
                navigation={props.history}
            />
            <ProgressBar now={42.5} className="progress-bar-custom" />
            <div className="containerData my-4" style={{padding:"4%"}}>
				<div className='textNormal'>Enter legal guardian PAN</div>
				<input
					className="uppercase normalInput"
					placeholder="ABCD1234E"
					autoFocus
					maxLength={10}
					id="pan"
					type="text"
					onChange={(e) => {
						setPan(e.target.value);
					}}
					value={pan}
				/>
				{
					pan.length===10?
					<div>
						{loader ? 
							<center>
								<Spinner
									as="span"
									animation="grow"
									size="sm"
									role="status"
									aria-hidden="true"
								/>
							</center>
						:
						<>{
							kycRegistered==='1'?
							<div>
								<p style={{color:'#6c6c6c'}}>Name: <b>{nameOnPan}</b></p>
								<AiFillCheckCircle color='#00A6A4' size={20}/><b> You are KYC registered</b>
							</div>
							:
							<div>
								<AiFillCloseCircle color='red' size={20}/> You are not KYC registered
								<p style={{marginTop:"4%"}}>Don’t worry, complete your account and we will help you get KYC registered online.</p>
							</div>
						}</>
						}
					</div>
					:
					""
				}
			
            </div>
            <div className='footer'>
                <BottomBackNextBtn
				nextDisabled={nextDisabled}
                    navigation={props.history}
                    buttonText="Confirm"
                    next="/activate-direct-mf/guardian-name"
                    params={{taxStatus:taxStatus}}
                    saveData={{
                        GuardianPan: {
                            pan,
							nameOnPan,
							kycRegistered
                        }
                    }}/>
            </div>
        </div>
    )
}

export default GuardianPan
