import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';

export default function WillingnessPage1(props) {

	const [willingnessQuestion1, setWillingnessQuestion1] = React.useState("0");

	const [loading, setLoading] = React.useState(false);

	const RadioInputOptions = [
		{ key: '0', name: 'Expected returns between 4% and 20%' },
		{ key: '1', name: 'Expected returns between 6% and 15%' },
		{ key: '2', name: 'Expected returns between 8% and 12%' },
	]

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setWillingnessQuestion1(u.wealthCreator.willingnessQuestion1 || '0')
				}
			}
			setLoading(false);
		});
	}, []);

	return (
		<div>
			<MainHeader
				header="Risk taking willingness"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={72} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textHead" style={{ fontWeight:"500" }}>Expected 5-year annual returns of 3 portfolios are given below.</div>
							<div className="textHead" style={{ fontWeight:"500",marginTop:"6%" }}>Which of the portfolios would you invest in for the next 5 years?</div>
							{RadioInputOptions.map((obj) => (
								<div key={obj.key} style={{ display: 'flex', flexDirection: 'row',marginTop:"6%" }}>
									<div className="radio-item">
										<input
											type="radio"
											id={obj.key}
											name="ritem"
											value={obj.key}
											onChange={(val) => {
												setWillingnessQuestion1(val.target.value)
												console.log(val.target.value)
											}}
											className="textNormal"
											checked={willingnessQuestion1 === obj.key}
										/>
										<label className="textNormal radio-label" htmlFor={obj.key}>{obj.name}</label>
									</div>
								</div>
							))}
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-willingness-2"
				params={props.location.state}
				buttonText="Continue"
				saveData={{
					wealthCreator: {
						willingnessQuestion1
					}
				}}
			/>
		</div>
	)
}
