import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';

export default function WillingnessPage2(props) {

	const [willingnessQuestion2, setWillingnessQuestion2] = React.useState("0");

	const [loading, setLoading] = React.useState(false);

	const RadioInputOptions = [
		{ key: '0', name: 'Moving to safe instruments completely and securing the corpus' },
		{ key: '1', name: 'No changes in portfolio. Stay invested for 2 more years for extra corpus' },
		{ key: '2', name: 'Moving half of the corpus to safe instruments and keeping the other half invested thereby neither completely losing out on potential returns nor risking the entire corpus' },
	]

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setWillingnessQuestion2(u.wealthCreator.willingnessQuestion2 || '0')
				}
			}
			setLoading(false);
		});
	}, []);

	return (
		<div>
			<MainHeader
				header="Risk taking willingness"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={81} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textHead" style={{ fontWeight:"500" }}>Your target retirement goal corpus has been accumulated 2 years in advance.</div>
							<div className="textHead" style={{ fontWeight:"500",marginTop:"6%" }}>What would your plan of action be?</div>
							{RadioInputOptions.map((obj) => (
								<div key={obj.key} style={{ display: 'flex', flexDirection: 'row' ,marginTop:"6%"}}>
									<div className="radio-item">
										<input
											type="radio"
											id={obj.key}
											name="ritem"
											value={obj.key}
											onChange={(val) => {
												setWillingnessQuestion2(val.target.value)
												console.log(val.target.value)
											}}
											className="textNormal"
											checked={willingnessQuestion2 === obj.key}
										/>
										<label className="textNormal radio-label" htmlFor={obj.key}>{obj.name}</label>
									</div>
								</div>
							))}
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-willingness-result"
				params={props.location.state}
				buttonText="Identify my risk taking willingness"
				saveData={{
					wealthCreator: {
						willingnessQuestion2
					}
				}}
			/>
		</div>
	)
}
