import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';


export default function CapabilityPage2(props) {

	const [dependents, setDependents] = React.useState("");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "0", "name": "0" },
		{ "key": "1", "name": "1" },
		{ "key": "2", "name": "2" },
		{ "key": "3", "name": "3" },
		{ "key": "4", "name": "4" },
		{ "key": "5", "name": "5" }
	]

	const checkValidation = () => {
		if (dependents) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [dependents])

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setDependents(u.wealthCreator.dependents || '')
				}
			}
			setLoading(false);
		});
	}, []);

	return (
		<div>
			<MainHeader
				header="Risk taking capability"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={18} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">Number of financial dependents</div>
							<input
								type="text"
								id="dependents"
								maxLength={2}
								className='normalInput'
								style={{color:"#294C88"}}
								placeholder='2'
								value={dependents}
								onChange={(num) => {
									const a = num.target.value.replace(/\D/g, '')
									setDependents(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row',overflowX:'auto' }}>
								{chipset.map((item) => {
									return (
										<Chip
											className="chip-b"
											label={item.name}
											onClick={() => {
												setDependents(item.name)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-capability-3"
				params={props.location.state}
				buttonText="Next"
				saveData={{
					wealthCreator: {
						dependents
					}
				}}
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
