import React from 'react';
import { Spinner } from 'react-bootstrap';
import Footer from '../Footer';
import { Chip } from '@mui/material';
import db from 'libs/db';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import { ProgressBar } from 'react-bootstrap';

export default function CapabilityPage4(props) {

	const [emergencyFund, setEmergencyFund] = React.useState("");

	const [loading, setLoading] = React.useState(false);
	const [nextDisabled, setNextDisabled] = React.useState(true);

	const chipset = [
		{ "key": "0", "name": "No emergency fund" },
		{ "key": "100000", "name": "₹ 1 Lakh" },
		{ "key": "200000", "name": "₹ 2 Lakh" },
		{ "key": "200000", "name": "₹ 3 Lakh" },
	]

	const checkValidation = () => {
		if (emergencyFund) {
			setNextDisabled(false);
		} else {
			setNextDisabled(true)
		}
	};

	React.useEffect(() => {
		checkValidation();
	}, [emergencyFund])

	React.useEffect(() => {
		setLoading(true)
		db.get(db.model.userData).then((u) => {
			console.log(u)
			if (u) {
				if (u.wealthCreator) {
					setEmergencyFund(u.wealthCreator.emergencyFund || '')
				}
			}
			setLoading(false);
		});
	}, []);

	return (
		<div>
			<MainHeader
				header="Risk taking capability"
				right=""
				screenType="WealthCreator"
				navigation={props.history}
			/>
			<ProgressBar now={36} className="progress-bar-custom" />
			<div className='containerData my-4'>
				{
					loading ?
						<center>
							<Spinner
								as="span"
								animation="grow"
								size="sm"
								role="status"
								aria-hidden="true" />
						</center>
						:
						<>
							<div className="textNormal">Size of your emergency fund</div>
							<input
								type="text"
								className='normalInput'
								style={{color:"#294C88"}}
								placeholder='1,00,000'
								id="emergencyFund"
								value={emergencyFund}
								onChange={(num) => {
									const a = num.target.value.replace(/\D/g, '')
									setEmergencyFund(a);
								}}
							/>
							<div style={{ display: 'flex', flexDirection: 'row' ,overflowX:"auto"}}>
								{chipset.map((item) => {
									return (
										<Chip
											className="chip-b"
											label={item.name}
											onClick={() => {
												setEmergencyFund(item.key)
											}}
										/>
									)
								})}
							</div>
						</>
				}
			</div>
			<Footer
				navigation={props.history}
				next="/wealth-creator/risk-capability-5"
				params={props.location.state}
				buttonText="Next"
				saveData={{
					wealthCreator: {
						emergencyFund
					}
				}}
				nextDisabled={nextDisabled}
			/>
		</div>
	)
}
