import { Button } from 'react-bootstrap';
import React from 'react';
import db from '../../../../../libs/db';
import { setUserData } from '../../../../../redux/CompleteProfile/actions';
import cssVar from 'views/mobileView/styles';

const Footer = ({
  next,
  navigation,
  params,
  nextDisabled = false,
  saveData,
  buttonText
}) => {

  const onNextPress = () => {
    if(nextDisabled){
      return
    }
    if (saveData) {
      db.get(db.model.userData).then((u) => {
        if (u) {
          if (u.wealthCreator) {
            Object.assign(u.wealthCreator, saveData.wealthCreator);
          } else {
            Object.assign(u, saveData);
          }
        } else {
          u = saveData
        }
        console.log(u)
        setUserData(u);
        navigation.push({ pathname: next, state: params })
      });
    } else {
      navigation.push({ pathname: next, state: params })
    }
  };

  return (
    <div className='footer'>
      <button
        style={{
          backgroundColor: cssVar.getPropertyValue('--brandSecondary'),
          borderRadius: "6px",
          color: "#fff",
          fontWeight: 500,
          height: "44px",
          flex: 1,
          width: "100%",
          opacity: nextDisabled ? 0.4 : 1
        }}
        onClick={() => onNextPress()}
      >
        {buttonText}
      </button>
    </div>
  );
};

export default Footer;
